import React, { useEffect, useState } from "react";
import { useLocation } from 'react-router-dom';
import { Link } from "react-router-dom";
import "../index.css";
import prof from "../images/profile.jpg"
export default function Profilebox(){
  const [backenddata, setBackendData] = useState([]);
  const [error, setError] = useState(null);
  const location = useLocation(); 
  const backendurl="https://backend2-n5wy.onrender.com"
  const username = localStorage.getItem('username');
  useEffect(() => {
   
    
    fetch(`${backendurl}/login/${username}`)
        .then(response => {
            if (!response.ok) {
                throw new Error('Failed to fetch data');
            }
            return response.json();
        })
        .then(data => {
            if (data.length > 0) {
                setBackendData(data[0]); // Assuming only one user is returned
            } else {
                throw new Error('No items found in the cart for this user');
            }
        })
        .catch(error => {
            setError(error.message);
            console.error('Error fetching data:', error);
        });
}, []);
return(
<div>
{!username  ? (
     <h1 style={{color:"black",textAlign:"center",marginTop:'200px',marginBottom:"200px"}}>Please <a href="/login">LOGIN</a> to get access to your profile</h1>
    ) : (
      <div className="profilebox">
      <img src="data:image/png;base64," alt="Profile"/> 
      <div className="detailsbox">
        <h2 style={{textAlign: "left"}}>Hey {backenddata.name}!</h2>
        <p>Full Name: {backenddata.name}</p>
        <p>Mail: {backenddata.email}</p>
        <p>You can access your cart items <a href="/cart">Here</a></p>
      </div>
    </div>
     
    )}
  </div>
)
}

